<template>
  <div :class="['modal', (showForm&&(rememberedOption===0))?'active':'hidden']" ref="default">
    <iframe id="hidden_frame"></iframe>
    <div class="form--container">
      <form ref="form" :action="formData.action" method="POST" target="hidden_frame" onsubmit="submitted=true;">
        <div>
          <h1>{{ formData.labels.contact[lang] }}</h1>
        </div>
        <div>
          <label for="name">{{ formData.labels.name[lang] }}<span>&nbsp;*</span>
            <transition name="fade">
              <span v-show="name.length===0&&clicked">
                <i>
                {{ formData.labels.validations.name[lang] }}
                </i>
              </span>
            </transition>
          </label>
          <input type="text" :placeholder="formData.labels.validations.name[lang]" id='name' autocomplete="off" class="form--input" v-model="name">
        </div>
        <div>
          <label for="email">Email<span>&nbsp;*</span>
            <transition name="fade">
              <span v-show="email.length===0&&clicked">
                <i>
                  {{ formData.labels.validations.email[lang] }}
                </i>
                </span>
            </transition>
          </label>
          <input type="email" placeholder="Email" id="email" autocomplete="off" class="form--input" v-model="email">
        </div>
        <div>
          <label for="message">{{ formData.labels.message[lang] }}</label>
          <textarea :placeholder="messagePlaceholder" id="message" rows="3" v-model="message"
                    class="form--input"></textarea>
        </div>
        <div class="button--container">
          <button class="about--item--background" type="button" @click="hideModal">
            {{ formData.labels.buttons.cancel[lang] }}
          </button>
          <button class="about--item--background md:mx-4" type="button" @click="sessionHide">
            {{ formData.labels.buttons.hide[lang] }}
          </button>
          <button class="inverted-background" type="button" @click="validate">
            {{ formData.labels.buttons.submit[lang] }}
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "ModalForm",
  props: ['formData', 'lang', 'showForm'],
  data() {
    return {
      name: '',
      email: '',
      message: '',
      clicked: 0,
      rememberedOption: sessionStorage.getItem('hidden_remember') | 0
    }
  },
  methods: {
    hideModal: function () {
      this.$refs.default.classList.remove("active");
      setTimeout(() => { //non-arrow function will receive this object as window object
        this.$refs.default.classList.add("hidden");
      }, 150) //duration-500
    },

    validate: function () {
      this.clicked = 1;
      if (this.$refs.form.reportValidity() && this.name.length && this.email.length) {
        this.$refs.form.submit();
      }
    },

    initializeInputNames: function () {
      for (let key in this.formData.inputs) {
        let element = this.$refs.form.querySelector(`#${key}`);
        element.setAttribute('name', this.formData.inputs[key]);
      }
    },
    sessionHide: function() {
      this.hideModal();
      sessionStorage.setItem('hidden_remember', 1);
      this.rememberedOption = 1;
    }
  },
  computed: {
    messagePlaceholder: function () {
      return this.message.length === 0 && this.clicked ? this.formData.labels.validations.message[this.lang] : this.formData.labels.message[this.lang];
    }
  },
  mounted: function () {
    this.initializeInputNames();
  },
}
</script>

<style scoped>
</style>